const animations = () => {
  const tlLoader = gsap.timeline();

  tlLoader
    .set('.logo__item', {
      scale: 0.5,
      opacity: 0
    })
    .to('.logo__item', {
      scale: 1,
      opacity: 1,
      duration: 1,
      stagger: 0.5
    });
    
  window.onload = function () {
    tlLoader
    .to('.logo__item', {
      scale: 0.5,
      opacity: 0,
      duration: 0.5,
      stagger: 0.5,
    })
    .to('.loader__container', {
      yPercent: -100,
      duration: 0.5
    });

    document.body.classList.remove('_lock');
  };
};

export default animations;

