import menuBuger from "./modules/burger-menu";
import changeBgMenu from "./modules/change-bg-menu";
import slider from "./modules/slider_1";
import sliderFlex from "./modules/slider_flex";
import rotateLogo from "./modules/rotate-logo";
import scrollTop from "./modules/show-scroll-top";
import smoothScrolling from "./modules/smooth-scrolling";
import showHeaderByScroll from "./modules/show-header";
import according from "./modules/accordind";
import sliderMain from "./modules/slider";
import showSubMenu from "./modules/show-submenu";
import accordingMenu from "./modules/according-menu";
import video from "./modules/playVideo";
import selectInput from "./modules/select-input";
import form from "./modules/form";
import linkDefault from "./modules/link-default";
import animations from "./modules/animations";


window.addEventListener('DOMContentLoaded', () => {
  'use strict';

  const logos = document.querySelectorAll('.logo-text');
  logos.forEach(item => {
    rotateLogo(item);
  });
  animations();
  showHeaderByScroll('.header', '.header__body');
  menuBuger('.menu__burger');
  video('.video__button img', '.overlay');
  form();
  linkDefault('a.button__block');

  selectInput('.select-persones', '.persone-form__select-clone',  '.persone-form', '.persone-form__view-select', '.button-book');
  selectInput('.select-time', '.time-form__select-clone', '.time-form', '.date-form__view-select', '.button-book');

  accordingMenu('.menu__item', '.menu__sublist');
  according('.menu-list__item', '.item-menu__text');

  showSubMenu('.menu__item', '.menu__sublist');

  smoothScrolling('.scroll-popup__body');
  smoothScrolling('.link-menu a');
  smoothScrolling('.menu__sublink');
  smoothScrolling('.order__book');

  changeBgMenu();

  scrollTop('.scroll-popup__body');

  sliderMain();
  sliderFlex({
    containerSelector: '.img-block-instagram__body',
    itemSelector: '.img-block-instagram__item',
    prev: '.btn.prev-btn',
    next: '.btn.next-btn',
    autoPlaySlider: true,
    playSliderByTime: 5000
  });
  slider({
    slidesPerView: 3,
    spaceBetween: 32,
    sliderSelector: '.blog__cards-block-body',
    itemSelector: '.card-block',
    play: true,
    timeoutSlider: 5000,
    breakpoints: {
      // when window width is <= 768px
      768: {
        slidesPerView: 1,
        spaceBetween: 0
      },
      // when window width is <= 1100px
      1100: {
        slidesPerView: 2,
        spaceBetween: 32
      }
    }
  });
});